import React, { createContext, useEffect, useState } from 'react';

export const CarritoContext = createContext();

const init = JSON.parse(localStorage.getItem('carrito')) || [];

const CarritoProvider = ( {children} ) => {
    const [carrito, setCarrito] = useState(init)
  
    const addToCarrito = (item) => {
      setCarrito( [...carrito, item] )
    }
    
    const removeItem = (itemId) => {
      const nuevoCarrito = carrito.filter( (prod) => prod.id !== itemId)
      setCarrito( nuevoCarrito )
    }

    const sumarUnoItem = (itemId) => {
        let nuevoCarrito = carrito;
        for (let i of nuevoCarrito) {
            if (i.id === itemId) {
                if (i.cantidad + 1 <= i.stock) {
                    i.cantidad ++;
                }
            }
        }
        setCarrito([...nuevoCarrito]);       
      }
      const restarUnoItem = (itemId) => {
        let nuevoCarrito = carrito;
        for (let i of nuevoCarrito) {
            if (i.id === itemId) {
                if (i.cantidad - 1 > 0) {
                    i.cantidad --;
                }
            }
        }
        setCarrito([...nuevoCarrito]);    
      }
  
    const calcularCantidad = () => {
      return carrito.reduce( (acc, prod) => acc + prod.cantidad, 0 )
    }

    const calcularTotal = () => {
      return carrito.reduce( (acc, prod) => acc + prod.cantidad * prod.precio, 0)
    }
    const estaEnCarrito = (itemId) => {
      return carrito.some( (prod) => prod.id === itemId)
    }
  
    const vaciarCarrito = () => {
      setCarrito([])
    }
    
    useEffect(()=>{
      localStorage.setItem('carrito', JSON.stringify(carrito))
    }, [carrito])
  
    return (
        <CarritoContext.Provider value={ {
            carrito,
            addToCarrito,
            removeItem,
            sumarUnoItem,
            restarUnoItem,
            calcularCantidad,
            vaciarCarrito,
            estaEnCarrito,
            calcularTotal
        }}>
            {children}
        </CarritoContext.Provider>
    )
}
export default CarritoProvider; 