import React, {useContext} from "react";
import './Carrito.css';
import { useHistory } from 'react-router';
import { CarritoContext } from '../../context/CarritoContext';
import { Container, Alert, Table, Button, } from "react-bootstrap";
import { Link } from "react-router-dom";
import { FaBackspace as Back, FaPlus, FaMinus, FaRegTrashAlt as Trash } from 'react-icons/fa';

export const Carrito = () => {
    const {carrito, vaciarCarrito, removeItem, calcularTotal, sumarUnoItem, restarUnoItem} = useContext(CarritoContext);
    const {goBack} = useHistory();
       
    return (
        <>
            <Container>
                {
                    carrito.length === 0
                    ?   <>
                            <Alert variant="warning m-3">No Hay Productos en el Carrito aún.</Alert>
                            <Link to="/" className="btn btn-success">Ir a comprar</Link>
                        </>
                    :    
                        <>           
                        <Table striped bordered hover variant="warning" size="sm">
                            <thead>
                                <tr>
                                    <th>PRODUCTO</th>
                                    <th>CANTIDAD</th>
                                    <th>TOTAL</th>
                                    <th>BORRAR</th>
                                </tr>
                            </thead> 
                            <tbody>
                                   {                                 
                                        carrito.map((prod) => (
                                            <tr key={prod.id}>
                                                <td><img src={process.env.PUBLIC_URL+prod.img}  className="imgCarrito" />
                                                     <span className="mx-3">{prod.id} - {prod.nombre}</span></td>
                                                <td><Button variant="light sm mx-3" onClick={() => restarUnoItem(prod.id)}><FaMinus color="#FF305D" size="15px"/></Button>
                                                    <span>{prod.cantidad}</span>
                                                    <Button variant="light sm mx-3" onClick={() => sumarUnoItem(prod.id)}><FaPlus color="#FF305D" size="15px"/></Button></td>
                                                <td>$ {Math.round(prod.cantidad * prod.precio, 0)}</td>
                                                <td><Button variant="danger mx-3" onClick={() => removeItem(prod.id)}><Trash color="white" size="25px"/></Button></td>
                                            </tr>                                        
                                        ))
                                    }                                    
                            </tbody>
                            <tfoot>
                                <tr>
                                    <td colSpan="2">IMPORTE TOTAL COMPRA</td>
                                    <td colSpan="2">$ {calcularTotal()}</td>
                                </tr>
                            </tfoot>
                        </Table>
                        <div className="row align-items-center mt-5">                  
                            <div className="col col-6">
                                <Button variant="secondary" onClick={() => goBack()}><Back color="white" size="30px"/> Volver A Comprar</Button>
                            </div>  
                            <div className="col col-6">
                                <Button variant="danger" onClick={vaciarCarrito}><Trash color="white" size="30px"/> Vaciar Carrito</Button>
                            </div>                  
                        </div>
                        </> 
                }
            </Container> 
        </>
    );

}